.header{
    display: flex;
    position:sticky;
    top:0;
    align-items: center;
    background-color: white;
    z-index: 100;
    width:100%;
    justify-content: space-between;
}
.header__icon{
    
    object-fit: contain;
    height:100px;
    margin-left: 20px;  
}
.header__center{
    
    display:flex;
    flex:1; 
    align-items: center;
    justify-content: center;
    max-width:fit-content;
    padding:10px;
    height:30px;
    border:1px solid lightgray;
    border-radius: 999px;
}
.header__center > input{
    border:none;
    padding:10px;
    width:250px;
    outline-width: 0;
    
}
.header__right{
    display:flex;
    align-items: center;
    justify-content: space-between;
    width:18vw;
    margin-right: 80px;

}