.banner{
    background: url("http://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/passairbnb-skiaspen-28328347.jpg") center center;
    height:60vh;
    position: relative;
}
.banner__search{
    display: flex;
    flex-direction: column;

}
.banner__searchButton{
    background-color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    color:#ff7779 !important

}
.banner__info{
    background-color: black;
    color:white;
    padding-left: 50px;
    padding-top: 25vh ;
    padding-bottom: 40px;
    padding-right: 50px;
    width:300px;
    
}
.banner__info > button{
    background-color: #ff7779;
    color: white;
    text-transform: inherit;
    margin-top: 10px;
    font-weight: 600;
}
.banner__info >button:hover{
    background-color: white;
    color:#ff7779;
}
.banner__info > h5{
    margin-top: 5px;
}